import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { ImageModalPage } from '../../image-modal/image-modal.page';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';
import { ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';
import { IndonesianMapPage } from 'src/app/indonesian-map/indonesian-map.page';

@Component({
  selector: 'app-sumatra-west-details',
  templateUrl: './sumatra-west-details.page.html',
  styleUrls: ['./sumatra-west-details.page.scss'],
})
export class SumatraWestDetailsPage implements OnInit {

  @ViewChild('Slides') slider: IonSlides;

  sliderOpts = {
    zoom: false,
    slidesPerView: 1,
    centeredSlides: false,
    spaceBetween: 20
  };

  images = ['sumatra_west/batik/batik1', 'sumatra_west/batik/batik2', 'sumatra_west/batik/batik3']

  constructor(private modalController: ModalController,
    private screenOrientation: ScreenOrientation,
    private alertController: AlertController) {
    this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
  }

  ngOnInit() {
    //
  }
  

  async presentAlertConfirm() {
    const alert = await this.alertController.create({
      header: 'If you want to see the batik motifs images you have to accept copyright request',
      message: 'Click Agree for see images',
      buttons: [
        {
          text: 'Disagree',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            IndonesianMapPage.isCopyrightAccepted = false;
          }
        }, {
          text: 'Agree',
          handler: () => {
            IndonesianMapPage.isCopyrightAccepted = true;
          }
        }
      ]
    });
    await alert.present();
  }

  openPreview(img) {
    if (!IndonesianMapPage.isAlreadyOpen) {
      this.presentAlertConfirm();
      IndonesianMapPage.isAlreadyOpen = true;
    }
    if (IndonesianMapPage.isCopyrightAccepted) {
      this.modalController.create({
        component: ImageModalPage,
        componentProps: {
          img: img
        }
      }).then(modal => modal.present());
    }
  }

  slideChanged() {
    let titleSlides = document.getElementById('titleSlides');
    this.slider.getActiveIndex().then(function (index) {
      switch (index) {
        case 0:
          titleSlides.innerHTML = "Felicità";
          break;
        case 1:
          titleSlides.innerHTML = "Panico";
          break;
        case 2:
          titleSlides.innerHTML = "Piacere";
          break;
      }
    });
  }

}
